.experiences {
  width: 100%;
  height: auto;
  align-self: center;
  padding: 20px 0;
  list-style: none;

  .experience {
    position: relative;
    width: 4px;
    height: 5%;
    background-color: #ccc;
    color: #000;
    margin: 0 auto;
    padding-top: 20px;
   

    &::after {
      background: #ffc10c;
      content: '';
      width: 25px;
      height: 25px;
      border-radius: 50%;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      bottom: 0;
     
    }

    &:last-child::after {
      @media (max-width: 768px) {
        display: none;
      }
    }

    div {
      position: relative;
      width: 250px;
      height: 150px;
      left: 10px;
      top: 10px;
      padding: 10px 5px;
      border-radius: 5px;
      background: #fff7df;
      visibility: visible;
      opacity: 1;
      transition: all 0.5s ease-in-out;
      max-height: calc(100% - 70px);
      display: flex;
      flex-direction: column;
      font-size: 1.1rem;

      h4 {
        font-weight: bold;
        font-size: 18px;
        display: block;
        margin-bottom: 10px;
      }

      span {
        img {
          width: 30px;
          height: auto;
          border-radius: 50%;
        }
      }

      &.visible div {
        transform: none;
        visibility: visible;
        opacity: 1;
      }
    }

    &:nth-child(odd) div {
      transform: translate3d(0, 0, 0);
      box-shadow: 0 1px 1px 2px #ffc10c;
      transition: all 2s ease;

      &:hover {
        cursor: pointer;
        transform: scale(2);
        z-index: 3;

        @media (max-width: 768px) {
          transform: scale(1.2);
        }
      }
    }

    &:nth-child(even) div {
      transform: translate3d(-265px, 0, 0);
      box-shadow: 0 1px 1px 2px #ffc10c;
      transition: all 2s ease;

      &:hover {
        cursor: pointer;
        transform: scale(2);
        z-index: 3;

        @media (max-width: 768px) {
          transform: scale(1.2);
        }
      }

      @media (max-width: 768px) {
        transform: translate3d(0, 0, 0);
      }
    }

    &:last-child div {
      @media (max-width: 768px) {
        display: none;
      }
    }
  }

  @media (max-width: 768px) {
    ul li div {
      width: 250px;
      * > { font-size: 1rem; }
    }

    ul li:nth-child(even) div {
      left: -289px;
    }
  }

  

  @media(min-width: 768px) and (max-width: 1150px) {
    .experience {
      &:last-child div {
          display: none;
        }
    
      &:last-child::after {
          display: none;
        }
      
    }
}

@media(max-width: 500px) and (max-height: 600px) {

  .experience {
    
    &:nth-child(2) div {
      display: none;
    }

  &:nth-child(2)::after {
      display: none;
    }
    &:last-child div {
        display: none;
      }
  
    &:last-child::after {
        display: none;
      }

      div {
        width: 200px;
        * > { font-size: 1rem; }
      }
  
      & > div {
        left: 10px;
      }
    
  }
 
  }
}

